import React, { useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import ReactGA from 'react-ga'
// import log from 'loglevel'

import { compose } from 'react-apollo'
// import { print } from 'graphql/language/printer'
import datastore from '@morphoto/datastore'
import { AppContext } from '@morphoto/client-common'

const withProfileConnect = Wrapped => {
  const Inner = ({ data }) => {
    const { seriesQ, watchedSeriesQ } = data
    const { currentUser, globalActions } = useContext(AppContext)

    const mySeries = seriesQ && seriesQ.userSeries && seriesQ.userSeries.seriesSummaries
      ? seriesQ.userSeries.seriesSummaries.filter(s => s.seriesStatus !== 'deleted') || []
      : []
    const watchingSeries = watchedSeriesQ && watchedSeriesQ.arraySeries ? watchedSeriesQ.arraySeries : []
    // log.info('ProfileConnect', { mySeries, watchedSeriesQ, watchingSeries })

    const loading = Boolean(
      (seriesQ && seriesQ.loading) ||
    (watchedSeriesQ && watchedSeriesQ.loading)
    )

    useEffect(() => {
      ReactGA.pageview('profile')
    }, [])

    return <Wrapped user={currentUser} {...{
      loading,
      mySeries,
      watchingSeries,
      actions: {
        jumpToSeries: globalActions.jumpToSeriesView,
        jumpToMap: globalActions.jumpToSeriesMap
      }
    }} />
  }

  Inner.propTypes = {
    data: PropTypes.object, // from graphql
    jumpToSeries: PropTypes.func, // from redux
    jumpToMap: PropTypes.func // from redux
  }
  return Inner
}

export default compose(
  datastore.graphql.queries.withuserSeriesQuery,
  // withMyPhotosQuery,
  datastore.graphql.queries.withMyWatchedSeriesQuery,
  withProfileConnect
)
